.Exchange-history-item {
  border-bottom: 1px solid #8d8d8d;
  display: -ms-flexbox;
  display: flex;
  padding: 12px;
  overflow: hidden; }
  .Exchange-history-item .item-container {
    width: 50%; }
    .Exchange-history-item .item-container .title-label {
      font-weight: bold;
      color: #d8d8d8; }
    .Exchange-history-item .item-container .tx-status.success {
      color: #75d08f;
      border-bottom: 1px solid #75d08f; }
    .Exchange-history-item .item-container .tx-status.pending {
      color: #ffd638;
      border-bottom: 1px solid #ffd638; }
    .Exchange-history-item .item-container .tx-status.reverted {
      color: #ff5b5b;
      border-bottom: 1px solid #ff5b5b; }



